.user-title {
  color: #000000;
  margin-bottom: 20px;
}

.user-name {
  font-style: normal;
  font-weight: 700;
  font-size: 45px;
  line-height: 55px;
  color: #000000;
  margin-top: 0;
  margin-bottom: 0;
}

.user-description {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  color: #595959;
  margin-top: 25px;
  width: 100%;
  max-width: 400px;
  margin-bottom: 0;
}

@media (max-width: 768px) {
  .user-name {
    font-size: 30px;
  }
  .user-description {
    margin-top: 10px;
  }
  .user-title {
    margin-bottom: 10px;
  }
}

